import { message} from 'antd';

const success = () => {
  message.success('This is a message of success');
};

const error = () => {
  message.error('This is a message of error');
};

const warning = () => {
  message.warning('This is message of warning');
};

const logindata = {
  auth: false,
  user:{}
}

//这是action

// const togglelogin = {
//   type: 'toggleAuth'
// }
//这是reducer
const reducer = (state = logindata, action) => {
    switch (action.type) {
        case 'toggleAuth':
            let user = action.user
            console.log(action)
          
            return state = {
              auth: true,
              user
            };
        case 'loginOut':
            return state = {
              auth: false,
              user:{}
            };
        default:
            return state;
    }
}
export default reducer;